
 
 
 
 
  
 body{
  background-color: #1f1e1e;
  color: white;
  overflow: hidden;
}

h1{
  text-align: center;
}

.todo-wrapper{
background-color: #343434;
padding: 2%;
width:fit-content;
margin-left: auto;
margin-right: auto;
margin-top: 3%;
max-height: 80vh;
overflow-y: auto;
box-shadow:0px 5px 7px rgb(27,27,27)
}

.todo-input{
  display: flex;
  align-items: center;
  justify-content:center;
  border-bottom: 1px solid rgb(78,78,78);
  padding-bottom: 25px;
  margin-bottom: 25px;

}
.todo-input-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 25px;
}

.todo-input-item label{
  font-weight: bold;
  margin-bottom: 10px;
}
.todo-input-item input{
  padding: 8px;
  border:none;
  width:250px;
}
.todo-input-item :focus{
  outline: 2px solid rgb(0, 203, 230); 
}

.btn{
  background-color: rgb(0, 203, 230);
  color:white;
  border-radius: 0px;
  border: none;
  margin-top:25px ;
  padding : 10px;
  width:60px;
   cursor: pointer;
}
.btn:hover{
  background-color: rgb(4,196,186);

}
.secbtn{
  background-color: rgb(71,71,71);
  color:white;
  border-radius: 0%;
  border: none;
  margin-top:25px ;
  padding :10px;
  width:fit-content;
   cursor: pointer;
}
.active{
  background-color: rgb(0, 203, 230);
}
.btn-area{
  margin-bottom: 15px;
}

.todolist{
  display: flex;
  flex-direction: column;
}
.todo-list-item{
  background-color: #414040;
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 25px;
   margin-bottom: 10px;
  padding-bottom: 10px;
  padding-top: 10px;
  box-shadow: 0px 3px 5px rgb(43,42,42);
 
}
.todo-list-item h3{
  font-size: 20px;
  color:rgb(0,203,230);
  font-weight: bold;
  margin:0px;
  margin-right: 0%;
  padding-right: 0%;
}
.todo-list-item p{

  font-size: 13px;
  color:rgb(161,161,161);
  margin-top: 8px;
  align-content: center;
  margin-right: 10px;
   
}
  
.bi-archive{
   font-size: 25px;
  cursor: pointer;
  
}
.bi-archive:hover{
  color: red;
}
.icon{
  display: flex;
  align-items: center;
}
.bi-clipboard2-check{
  margin-left: 10px;
  font-size:22px;
  cursor: pointer;
  color:rgb(0,203,230);
  
}


 












